export default {
	props: {
		item: Object,
		index: {
			type: Number,
			default: -1},
		isDetail:{
			type: Boolean,
			default: false
		}
	},
	methods:{
		// 关注功能
		follow(){
			//点击时通知父组件
			this.$emit('follow', this.index)
		},
		//评论
		doComment(){
			//不在详情页
			if(!this.isDetail){
				return this.openDetail()
			}
			this.$emit('doComment')
		},
		//分享
		doShare(){
			//不在详情页
			if(!this.isDetail){
				return this.openDetail()
			}
			this.$emit('doShare')
		},
		//打开个人空间
		openSpace(){
			console.log('进入个人空间');
		},
		//打开详情页
		openDetail(){
			//处于详情页
			if(this.isDetail){
				return
			}
			uni.navigateTo({
				url: '../../pages/detail/detail?detail=' + JSON.stringify(this.item),
			});
		},
		//点赞与取消赞操作
		doSupport(type){
			this.$emit('doSupport',{
				type: type,
				index: this.index
			})
			
		}
	}
}
